<!-- eslint-disable vuejs-accessibility/heading-has-content -->
<template>
  <section class="d-flex flex-column align-items-center justify-content-center h-100">
    <h4>{{ date.week }} {{ date.today }} {{ date.dateMonth }}</h4>
    <h5 v-if="date.week === ('SUN' || 'FRI' || 'SAT')">
      12<span class="timePoint">:</span>00PM - 10<span class="timePoint">:</span>00PM
    </h5>
    <h5 v-else-if="date.week === ('WED' || 'THURS')">
      2<span class="timePoint">:</span>00PM - 8<span class="timePoint">:</span>00PM
    </h5>
    <h5 v-else><span ref="restToday"></span><i class="bi bi-pencil-fill"></i></h5>
    <div class="mb-3">
      <VCalendar :attributes="attributes" class="border-0" />
    </div>
    <div class="d-flex align-items-center" style="margin-bottom:-15px">
      <i class="bi bi-dot h3 text-danger mb-0"></i>
      <h6>- Public Holiday</h6>
    </div>
    <div class="d-flex align-items-center" style="margin-bottom:-15px">
      <i class="bi bi-dot h3 text-info mb-0"></i>
      <h6>- 10:00AM - 2:00PM</h6>
    </div>
    <div class="d-flex align-items-center">
      <i class="bi bi-dot h3 text-secondary mb-0"></i>
      <h6>- 4:00PM - 10:00PM</h6>
    </div>
  </section>
</template>

<script>
import { gsap } from 'gsap';

export default {
  data() {
    return {
      newDate: new Date(),
      openTime: true,
      week: ['SUN', 'MON', 'TUES', 'WED', 'THURS', 'FRI', 'SAT'],
      date: {
        dateYears: '',
        dateMonth: '',
        today: '',
        week: '',
      },
      attributes: [
        {
          highlight: {
            color: 'gray',
            fillMode: 'outline',
          },
          dates: new Date(),
        },
        {
          dates: { repeat: { weekdays: [2, 3] } },
          dot: { color: 'red' },
        },
        {
          dates: { repeat: { weekdays: [1, 6, 7] } },
          dot: {
            style: {
              backgroundColor: '#94ACD4',
            },
          },
          // popover: { label: 'Take Noah to basketball practice.' },
        },
        {
          dates: { repeat: { weekdays: [1, 4, 5, 6, 7] } },
          dot: {
            style: {
              backgroundColor: '#26498d',
            },
          },
        },
      ],
    };
  },
  methods: {
    checkOpen() {
      if (this.date.week === ('TUES' || 'MON')) {
        this.attributes[0].highlight.color = 'red';
      }
    },
  },
  mounted() {
    this.date.dateYears = this.newDate.getFullYear();
    this.date.dateMonth = this.newDate.toLocaleString('en-US', { month: 'short' });
    this.date.today = this.newDate.getDate();
    this.week.forEach((item, i) => {
      if (i === this.newDate.getDay()) {
        this.date.week = item;
      }
    });
    this.checkOpen();
    gsap.to('.timePoint', { opacity: 0, duration: 1.4, repeat: -1 });
    const timeline = gsap.timeline();
    timeline.to(this.$refs.restToday, {
      duration: 3,
      text: { value: 'Public Holiday', delimiter: '' }, // delimiter加空格=非單字
      repeat: -1,
    });
  },
};
</script>
