<template>
  <div class="notepad content-box">
    <div class="top rounded-0">
      <h3 class="text-uppercase text-center text-nowrap">shopping list</h3>
    </div>
    <div class="paper d-flex flex-column justify-content-between">
      <table class="table table-borderless">
        <thead>
          <tr>
            <th scope="col">Product</th>
            <th scope="col">Size</th>
            <th scope="col">Price</th>
            <th scope="col">Qty</th>
            <th scope="col">Sub</th>
            <th scope="col">Alt</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="(item, index) in cartsList" :key="index">
            <tr>
              <td class="col-4">{{ item.product.title }}</td>
              <template v-for="(data, key) in item.option" :key="key">
                <template v-if="key === 0">
                  <td class="col-2">{{ data.info.size }}</td>
                  <td class="col-2">${{ data.info.price }}</td>
                  <td class="col-2"><a href="#" class="text-decoration-none">-</a
                    ><span class="luluText">{{ data.quantity }}</span
                    ><a href="#" class="text-decoration-none">+</a></td>
                  <td class="col-2">${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                  <td class="col-1 text-center"><a href="#" @click.prevent="openModal(item, data)"
                      ><i class="bi bi-pencil"></i></a
                  ></td>
                </template>
              </template>
            </tr>
            <tr class="luluText" v-if="item.option.length !== 1">
              <td colspan="1"></td>
              <template v-for="(data, key) in item.option" :key="key">
                <template v-if="key === 1">
                  <td>{{ data.info.size }}</td>
                  <td>${{ data.info.price }}</td>
                  <td><a href="#" class="text-decoration-none">-</a
                    ><span class="luluText">{{ data.quantity }}</span
                    ><a href="#" class="text-decoration-none">+</a></td>
                  <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                  <td class="col-1 text-center"><a href="#" @click.prevent="openModal(item, data)"
                      ><i class="bi bi-pencil"></i></a
                  ></td>
                </template>
              </template>
            </tr>
            <tr class="luluText" v-if="item.option.length !== 1">
              <td colspan="1"></td>
              <template v-for="(data, key) in item.option" :key="key">
                <template v-if="key === 2">
                  <td>{{ data.info.size }}</td>
                  <td>${{ data.info.price }}</td>
                  <td><a href="#" class="text-decoration-none">-</a
                    ><span class="luluText">{{ data.quantity }}</span
                    ><a href="#" class="text-decoration-none">+</a></td>
                  <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                  <td class="col-1 text-center"><a href="#" @click.prevent="openModal(item, data)"
                      ><i class="bi bi-pencil"></i></a
                  ></td>
                </template>
              </template>
            </tr>
            <br>
          </template>
        </tbody>
      </table>
      <div class="modal-footer pb-0 d-flex flex-column align-items-end">
        <div class="mb-2">
          Total：$ <span class="luluText">{{ this.$toCurrency(1000) }}</span>
        </div>
        <div class="d-flex justify-content-between w-100">
          <router-link to="/shop" class="font-monospace fw-bold titleText position-relative">
            <span ref="shop"></span><i class="bi bi-pencil-fill"></i>
          </router-link>
          <router-link
            to="/shop/checkout"
            class="font-monospace fw-bold titleText position-relative"
            ><i class="bi bi-caret-right-fill" ref="checkout"></i>Checkout
          </router-link>
        </div>
      </div>
    </div>
  </div>
  <EditCart ref="editCartModal" />
</template>

<style lang="scss" scoped>
@import '@/assets/stylesheets/helpers/_label.scss';
.table > :not(caption) > * > * {
  background-color: transparent;
  padding: 0;
}
</style>

<script>
import { gsap } from 'gsap';
import EditCart from '@/components/models/cart/EditModal.vue';
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      cartsList: [],
      cartModal: '',
    };
  },
  components: {
    EditCart,
  },
  methods: {
    getCarts() {
      emitter.emit('loadingStatus', true);
      const api = `${process.env.VUE_APP_API}/carts`;
      this.$http
        .get(api)
        .then((res) => {
          this.cartsList = res.data.data.carts;
          // 資料要填寫一下，我後端技術不好ＱＱ
          this.populateCarts(res.data.data.carts);
          emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          emitter.emit('loadingStatus', false);
        });
    },
    populateCarts(data) {
      // 先將選項拉出，比對product裡頭的選項，將正確得填入選項
      data.forEach((item) => {
        item.option.forEach((e) => {
          item.product.options.forEach((index) => {
            // eslint-disable-next-line no-underscore-dangle
            if (index._id === e.size) {
              e.info = index;
            }
          });
        });
      });
    },
    openModal(title, data) {
      this.$refs.editCartModal.openModal(title, data);
    },
  },
  mounted() {
    this.getCarts();
    emitter.on('getCarts', () => {
      this.getCarts();
      console.log('ok');
    });
    const timeline = gsap.timeline();
    timeline
      .to(this.$refs.shop, {
        duration: 3,
        text: { value: 'Shopping', delimiter: '' },
        repeat: -1,
      })
      .to(
        this.$refs.checkout,
        {
          opacity: 0,
          duration: 1.5,
          repeat: -1,
        },
        '<',
      );
  },
};
</script>
