<template>
  <!-- new -->
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref="editModalNew"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content cartBG rounded-0 border-3 border-primary">
        <div class="modal-header border-3 border-primary">
          <h1 class="modal-title fs-5">{{ tempProduct.title }}</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body row">
          <div class="col-6">
            <img :src="tempProduct.image" alt="" class="mb-3" />
          </div>
          <div class="col-6"></div>
        </div>
        <div class="modal-footer border-3 border-primary d-flex justify-content-between">
          <button
            type="button"
            class="btn btn-outline-danger rounded-0"
            @click="deleteProduct(tempProduct._id, tempOption._id)"
          >
            Delete Product
          </button>
          <button type="button" class="btn btn-primary rounded-0">Update</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      modal: '',
      tempOption: {},
      tempProduct: {},
    };
  },
  methods: {
    openModal(product, option) {
      this.tempProduct = product.product;
      this.tempOption = option;
      this.modal.show();
    },
    closeModal() {
      this.modal.hide();
    },
    deleteProduct(productId, optionId) {
      emitter.emit('loadingStatus', true);
      const api = `${process.env.VUE_APP_API}/cart`;
      this.$http
        .delete(api, { data: { productId, optionId } })
        .then((res) => {
          console.log(res);
          emitter.emit('getCarts', res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.closeModal();
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.editModalNew);
  },
};
</script>
